import Component from 'vue-class-component'
import {Model, Vue} from 'vue-property-decorator'
import {IndicatorValue} from "@/shared/loader";
import {CriminalCharts} from "@/shared/charts";

@Component
export default class RepublicMap extends Vue {

    @Model()
    public model!: IndicatorValue[]

    public value(valueArea: string): string {
        const result = this.model.find(item => item.area === valueArea)
        if (typeof result !== 'undefined') {
            return result.value.toLocaleString()
        }
        return this.$t('na').toString()
    }

    get regions() {
        return CriminalCharts.REGIONS
    }

    public onAreaClick(area: string) {
        this.$emit("onAreaClick", area)
    }

}
