import Component from 'vue-class-component'
import {Vue, Watch} from 'vue-property-decorator'
import LineIndicator from "@/model/indicators/LineIndicator"
import BarIndicator from "@/model/indicators/BarIndicator"

import {CriminalData} from "@/shared/loader";
import {CriminalCharts, IndicatorChart} from "@/shared/charts";

import CriminalMap from "@/views/indicators/criminal/map/index.vue";

@Component({
    components: {
        'v-line-indicator': LineIndicator,
        'v-bar-indicator': BarIndicator,
        'v-criminal-map': CriminalMap
    },
})
export default class Criminal extends Vue {

    private preparingData = true
    private chartTotalVsElectronically: IndicatorChart | undefined
    private chartMisdemeanorVsCrime: IndicatorChart | undefined
    private chartBySeverity: IndicatorChart | undefined
    private chartPersons: IndicatorChart | undefined

    private chartKills: IndicatorChart | undefined
    private chartTheft: IndicatorChart | undefined
    private chartFraud: IndicatorChart | undefined
    private charPillage: IndicatorChart | undefined
    private charRobbery: IndicatorChart | undefined
    private chartDisorder: IndicatorChart | undefined
    private chartBlackmail: IndicatorChart | undefined
    private chartRape: IndicatorChart | undefined
    private chartDrugs: IndicatorChart | undefined


    async mounted() {
        this.preparingData = true
        // загружаем и парсим данные
        await CriminalData.fetch()
        this.generateChartsData()
        this.preparingData = false
    }

    public selectedRegion = '19'

    @Watch('selectedRegion')
    onRegionChanged(value: string) {
        this.selectedRegionAreas.splice(1)

        if (this.selectedRegion !== '19') {
            CriminalCharts.getAreas(this.selectedRegion).map(areaId => this.selectedRegionAreas.push({
                value: areaId,
                text: this.$i18n.t(`areas.${areaId}`).toString()
            }))
        }

        if (this.selectedArea === '0') {
            this.generateChartsData()
        } else {
            this.selectedArea = '0'
        }
    }

    @Watch('selectedArea')
    onAreaChanged(value: string) {
        this.generateChartsData()
    }

    public selectedRegionAreas =  [{value: '0', text: 'Все районы'}]

    public selectedArea = '0'

    get regions() {
        const regions = [{ value: "19", text: 'Республика Казахстан' }]
        CriminalCharts.REGIONS.map(regionId => regions.push({ value: regionId, text: this.$i18n.t(`regions.${regionId}`).toString() }))
        return regions
    }

    private generateChartsData() {
        let areaOrRegion = this.selectedArea

        if (areaOrRegion === '0') {
            areaOrRegion = this.selectedRegion
        }

        this.chartTotalVsElectronically = CriminalCharts.chartTotalVsElectronically(CriminalData.getYearsData(1, areaOrRegion), CriminalData.getYearsData(2,areaOrRegion))

        this.chartMisdemeanorVsCrime = CriminalCharts.chartMisdemeanorVsCrime(CriminalData.getYearsData(3,areaOrRegion), CriminalData.getYearsData(4,areaOrRegion))

        this.chartBySeverity = CriminalCharts.chartBySeverity(
            CriminalData.getYearsData(5,areaOrRegion),
            CriminalData.getYearsData(6,areaOrRegion),
            CriminalData.getYearsData(7,areaOrRegion),
            CriminalData.getYearsData(8,areaOrRegion))

        this.chartKills = CriminalCharts.chartKills(CriminalData.getYearsData(9,areaOrRegion))
        this.chartTheft = CriminalCharts.chartTheft(CriminalData.getYearsData(10,areaOrRegion))
        this.chartFraud = CriminalCharts.chartFraud(CriminalData.getYearsData(11,areaOrRegion))
        this.charPillage = CriminalCharts.charPillage(CriminalData.getYearsData(12,areaOrRegion))
        this.charRobbery = CriminalCharts.charRobbery(CriminalData.getYearsData(13,areaOrRegion))
        this.chartDisorder = CriminalCharts.chartDisorder(CriminalData.getYearsData(14,areaOrRegion))
        this.chartBlackmail = CriminalCharts.chartBlackmail(CriminalData.getYearsData(15,areaOrRegion))
        this.chartRape = CriminalCharts.chartRape(CriminalData.getYearsData(16,areaOrRegion))
        this.chartDrugs = CriminalCharts.chartDrugs(CriminalData.getYearsData(17,areaOrRegion))

        this.chartPersons = CriminalCharts.chartPersons(CriminalData.getYearsData(18,areaOrRegion))
    }

}
